import React, { useEffect } from 'react'
import { Chart as ChartJS, RadialLinearScale, ArcElement, Tooltip, Legend } from 'chart.js'
import { Doughnut, PolarArea } from 'react-chartjs-2'
import { useSelector } from 'react-redux';
import { store } from '../store/store';
import { loadToys } from '../store/toy.action';

export function HomePage() {
    const toys = useSelector((storeState) => storeState.toyModule.toys)
    ChartJS.register(ArcElement, RadialLinearScale, Tooltip, Legend)
    const labels = ['On wheels', 'Box game', 'Art', 'Baby', 'Doll', 'Puzzle', 'Outdoor', 'Battery Powered']

    useEffect(() => {
        loadToys()
    }, [])
    function getChartsData() {
        const chartsData = toys.reduce(
            (acc, toy) => {
                toy.labels.forEach((label) => {
                    acc.labelsCountMap[label] = acc.labelsCountMap[label] ? ++acc.labelsCountMap[label] : 1
                    acc.labelsPriceMap[label] = acc.labelsPriceMap[label] ? (acc.labelsPriceMap[label] += toy.price) : toy.price
                })

                return acc
            },
            { labelsCountMap: {}, labelsPriceMap: {} }
        )
        Object.keys(chartsData.labelsPriceMap).forEach((label) => (chartsData.labelsPriceMap[label] /= chartsData.labelsCountMap[label]))

        return chartsData
    }
    const { labelsPriceMap, labelsCountMap } = getChartsData()

    const data = {
        labels: labels,
        datasets: [
            {
                label: 'avarage price of toys',
                data: Object.values(labelsPriceMap),
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)',
                    'rgba(273, 264, 64, 0.2)',
                    'rgba(255, 360, 64, 0.2)',
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)',
                    'rgba(273, 264, 64, 0.2)',
                    'rgba(255, 360, 64, 0.2)',
                ],
                borderWidth: 1,
            },
        ],
    }
    const data2 = {
        labels: labels,
        datasets: [
            {
                label: 'count of toys in label',
                data: Object.values(labelsCountMap),
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)',
                    'rgba(273, 264, 64, 0.2)',
                    'rgba(255, 360, 64, 0.2)',
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)',
                    'rgba(273, 264, 64, 0.2)',
                    'rgba(255, 360, 64, 0.2)',
                ],
                borderWidth: 1,
            },
        ],
    }
    return <section className="home-page">
        <div style={{ width: '330px' }} className="chart">
            <Doughnut data={data} />
        </div>
        <div style={{ width: '330px' }} className="chart">
            <PolarArea data={data2} />
        </div>

    </section >
}